<template>
  <div>
    <h3></h3>
    <div class="field">
      <label></label>
      <input type="text" v-model="department.name" />
    </div>
    <div class="field">
      <label></label>
      <input type="text" v-model="department.description" />
    </div>
    <div class="btn-group">
      <button @click="deleteDepartment"></button>
      <button @click="updateDepartment"></button>
    </div>
    <Snackbar />
  </div>
</template>

<script>
import Snackbar from "../../../components/Snackbar";
// import firebase from "firebase";
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["department"])
  },
  methods: {
    updateDepartment() {
      this.$store.dispatch("updateDepartment", this.department);
    },
    deleteDepartment() {
      this.$store.dispatch("deleteDepartment", this.department.id);
    }
  },
  components: {
    Snackbar
  }
};
</script>
