<template>
  <div>
    <h3></h3>
    <div class="field">
      <label></label>
      <input type="text" v-model="group.name" />
    </div>
    <div class="field">
      <label></label>
      <input type="text" v-model="group.description" />
    </div>
    <div class="field">
      <label> </label>
      <input type="text" v-model="group.site" />
    </div>
    <div class="btn-group">
      <button @click="deleteGroup"></button>
      <button @click="updateGroup"></button>
    </div>
    <Snackbar />
  </div>
</template>

<script>
import Snackbar from "../../../components/Snackbar";
// import firebase from "firebase";
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["group"])
  },
  methods: {
    updateGroup() {
      this.$store.dispatch("updateGroup", this.group);
    },
    deleteGroup() {
      this.$store.dispatch("deleteGroup", this.group.id);
    }
  },
  components: {
    Snackbar
  }
};
</script>
